<template lang="pug">
  .form-field
    label.form-field-block(:class="{error: isInvalid}")
      .form-label(v-if="label")
        .form-label-text {{ label }}
        .form-label-info.flex-center(v-if="info")
          icon(name="question")
      slot.form-input
    collapse.form-errors(:value="isCollapsed")
      .form-error(v-for="(message, index) in errors") {{ message }}
</template>

<script>
export default {
  name: 'form-field',

  props: {
    label: {type: String},
    info: {type: String, default: ''},
    errors: {type: Array, default: () => ([])},
  },

  data: () => ({
    isCollapsed: false,
  }),

  computed: {
    isInvalid() {
      return !!(this.errors && this.errors.length)
    }
  },

  watch: {
    isInvalid() {
      this.$nextTick(() => this.isCollapsed = !!this.isInvalid)
    },
  },
}
</script>
